<template>
  <div id="content">
    <Header title="智能查验记录列表"></Header>

    <div class="top_search">
<!--      <p>查询查验记录</p>-->
      <p><input style="width: 300px;height: 20px" v-model="key" type="text" placeholder="请输入公司或个人名称"></input>
        <button class="search" style="margin-left: -10px;height: 30px;letter-spacing: 3px;background-color: #0c4469;font-weight: bolder;font-size: 13px" @click="search">搜索</button>
      </p>
    </div>

    <div id="table">
      <table>
        <tr class="title_manages">
          <template v-for="item in headList">
            <th>{{item}}</th>
          </template>
        </tr>


        <template v-for="(item, index) in records">
          <tr class="trData">
            <td width="25%">{{ item.name }}</td>
            <td width="15">{{ item.id }}</td>
            <td width="10%">{{ item.type }}</td>
            <td width="10%">{{ item.qualified }}</td>
            <td width="10%">{{ item.creditScore }}</td>
            <td width="10%">{{ item.createTime }}</td>
            <td width="10%">{{ item.history }}次</td>
            <td width="10%">
              <span @click="look(item.id,item.name,item.code,item.folk,item.type)" class="accept">{{act}}</span>
<!--              <span @click="refuse(item.id)" class="refuse">拒绝</span>-->
            </td>
          </tr>
        </template>
      </table>
    </div>

    <div class="bottom_button">
      <input style="width: 100px;height: 30px;font-size: 15px;letter-spacing: 5px;font-weight: bold;background-color: #0c4469;color: white" type="button" value="返回" @click="back"></input>
    </div>

  </div>
</template>

<script>
import Header from '../../components/Header'
export default {
  name: "index",
  components: {
    Header,
  },
  data: function (){
    return {
      act: '查看',
      checkParam: {
        ruleList:[],
        information:[],
        name: null,
        code: null,
        legalPersonName: null,
        reportId: null
      },
      person:{
        name: '范伟',
        idCard: null,
        accountNo: null,
        bankPreMobile: null,
      },
      key: '', //搜索关键字
      headList: ['交易主体名称', '交易主体帐号', '主体类型', '查验结果',
        '综合信用评分', '报告生成时间', '历史记录', '查看详情'],
      // name, account, type, result, score, time, records, opeartion
      records: [
        // {
        //   "name": '***有限责任公司',
        //   "account": '20210001',
        //   "type": '企业',
        //   "result": '合格',
        //   "score": '85.7',
        //   "time": '2021.01.01',
        //   "records": '7',
        // },
      ], // 请求记录数据
    }
  },
  created() {
    /*
    获取
     */
  },
  activated() {
    let _this = this;
    let id = window.sessionStorage.getItem('user')
    // console.log(id)
    // console.log()
    _this.$request({
      url: '/check/list?id='+id,
      method: 'GET'
    }).then(response => {
      if (response.code === 200){
        let data = response.data;
        // console.log("res:" + JSON.stringify(response.data))
        _this.records = data;
        // console.log(data);
      }
    })
    // _this.look();
  },
  methods: {
    //查验id,企业名,企业code,法人
    look: function (id,name,code,folk,type){
      let _this = this;
      if( type === '个人'){
        _this.$router.push({
          path: '/report/individual',
          query:{
          rules1:JSON.stringify(_this.person)
        }
        })
      } else {

        _this.msgSuccess("正在获取数据，请稍等");
        _this.$request({
          url: '/check/checkRecord?id=' + id,
          method: 'get',
          data: null,
        }).then(response => {
          if (response.code === 200) {
            // console.log(123)
            // console.log('123:' + response.data)

            // for (let item in this.checkParam){
            //   console.log("checkParam:" + item.valueOf())
            // }
            // console.log(this.checkParam)
            // console.log(this.checkParam.name)
            // console.log(this.checkParam.code)
            // console.log(this.checkParam.legalPersonName)
            // console.log(this.checkParam.reportId)

            window.localStorage.setItem("enRule", response.data.enrule);
            window.localStorage.setItem("enInfor", response.data.eninfor);


            _this.checkParam.name = name;
            _this.checkParam.code = code;
            _this.checkParam.legalPersonName = folk;
            _this.checkParam.reportId = id
            _this.checkParam.ruleList = JSON.parse(window.localStorage.getItem('enRule'));
            _this.checkParam.information = JSON.parse(window.localStorage.getItem('enInfor'));
            console.log(_this.checkParam.ruleList)
            console.log(_this.checkParam.information)
            // console.log(".1.1:" + _this.checkParam)
            // console.log(JSON.stringify(_this.checkParam))
            _this.$request({
              url: '/check/rule',
              method: "post",
              data: _this.checkParam
            }).then(response => {

              if (response.code === 200) {
                let data = response.data;
                // console.log('data:'+ JSON.stringify(data));
                _this.$router.push({
                  path: '/report/enterprise',
                  query: {
                    // checkParam: JSON.stringify(_this.checkParam),
                    checkParam: JSON.stringify(data)
                  },
                });
              } else {
                _this.msgError("服务器异常")
              }
            });


          } else {
            _this.msgError("服务器异常");
          }
        });
      }
      // _this.checkParam.name=name;
      // _this.checkParam.code=code;
      // _this.checkParam.legalPersonName=folk;
      // _this.checkParam.reportId = id
      // _this.checkParam.ruleList=JSON.parse(window.localStorage.getItem('enRule'));
      // _this.checkParam.information=JSON.parse(window.localStorage.getItem('enInfor'));
      // console.log(_this.checkParam.ruleList)
      // console.log(_this.checkParam.information)
      // // console.log(".1.1:" + _this.checkParam)
      // // console.log(JSON.stringify(_this.checkParam))
      // _this.$request({
      //   url: '/check/rule',
      //   method: "post",
      //   data: _this.checkParam
      // }).then(response => {
      //
      //   if (response.code === 200) {
      //     let data = response.data;
      //     console.log('data:'+ JSON.stringify(data));
      //     _this.$router.push({
      //       path: '/report/enterprise',
      //       query: {
      //         // checkParam: JSON.stringify(_this.checkParam),
      //         checkParam: JSON.stringify(data)
      //       },
      //     });
      //   } else {
      //     _this.msgError("服务器异常")
      //   }
      // });
    },

    search: function (){
      /*
      搜索 重新请求数据
       */
    },

    back: function (){
      history.back();
    }
  },
}
</script>

<style scoped>
#content{
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}

.top_search {
  margin: 20px auto 10px 135px;
  /*width: 300px;*/
}

.search {
  border: 1px solid #0c4469;
  color: white;
  background-color: #0c4469;
}

.bottom_button {
  margin: 40px 45% 10px 45%;
}

#table {
  padding: 0 10px;
  width: 83%;
  margin-left: auto;
  margin-right: auto;
}

.title_manages th{
  padding: 10px;
}

.trData td{
  padding: 15px;
  font-size: 15px;
}

input {
  border: 1px solid #008cd5;
  padding: 5px;
  border-radius: 3px;
  /* 棱角变圆 */
  margin-right: 15px;
}

#table table {
  width: 100%;
  font-size: 15px;
  border: 1px solid #eee;
  text-align: center;
  border-collapse: collapse;
}

table th {
  background-color: #f5f5f5;
  border-right: 1px solid #eee;
  font-size: 17px;
}

/*
    #fff
    #f5f5f5
    #eee
    颜色深度依次递增
 */

table td {
  padding: 10px;
  border-bottom: 1px solid #eee;
  border-right: 1px solid #eee;
}

.refuse {
  color: red;
}

.accept {
  color: #008cd5;
}

tr td span {
  cursor: pointer;
  /*  指定鼠标指引 */
  margin: 10px;
}


.title span {
  float: right;
  cursor: pointer;
}


.content input {
  width: 270px;
  margin-bottom: 15px;
}
</style>



